t<template>
  <div class="mailWrap">
      <table>
          <tr>
              <th>SMS발송</th>
          </tr>
          <tr>
              <th>보내는이</th>
              <td><input type="text" value="01011221122"></td>
          </tr>
          <tr>
              <th>받는이</th>
              <td><textarea cols="27" rows="3"></textarea></td>
          </tr>
          <tr>
              <th>내용</th>
              <td><textarea cols="27" rows="10"></textarea></td>
          </tr>
      </table>
      <div class="button__section">
            <div>
                <button @click="hyper('')">발송하기</button>
                <button @click="close">취소</button>
            </div>
        </div>
  </div>
</template>

<script>

export default {
  name: 'SmsPopup',
  data () {
    return {}
  },
  methods: {
    close () {
      window.open('about:blank', '_self').close()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 .mailWrap {
  padding: 10px;
}
input {
    width: 208px;
}
 table {
    margin-top: 10px;
 }
 table > tr > th {
     width: 3000px;
 }
 table > tr > td {
     width: 500px;
 }
.tableContent {
    width: 100%;
    text-align: center;
}
.Mail > td button {
    border-radius: 5px;
    width: 100px;
}
.Mail > td {
    line-height: 2;
}
.address {
    width: 400px;
}
.mailInput {
    width: 60px;
}
.td__size {
    width: 500px;
}
</style>



